import React, { useState } from 'react';
import useMediaQuery from '@mui/material/useMediaQuery';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import Link from '@material-ui/core/Link';
import GitHubIcon from '@mui/icons-material/GitHub';

const useStyles = makeStyles((theme) => ({
    root: {
      flexGrow: 1,
    },
    paper: {
      height: 300,
      backgroundColor: '#1f2933a7',
    },
    header: {
        paddingRight: 10,
        paddingTop: 10,
        paddingLeft: 10,
    },
    icons: {
        float: "right",
        paddingLeft: 10,
    },
    title: {
        color: "#03dac6",
    },
    content: {
        color: "#03dac6",
    },
    '& ul': {
        color: "white",
        display: "flex",
    }

  }));

  function Content(props){

    const classes = useStyles();
    return(
        <div>
            <div className={classes.header}>
                
                <Link href={props.url} class='link' target="_blank"> <GitHubIcon className={classes.icons} sx={{ fontSize: 35 }} /> </Link>
                <h1 className={classes.title}>{props.title}</h1>
                <p className={classes.content}>{props.content}</p> 
                <ul className={classes.tags}>
                    <li>test</li>
                    <li>test</li>
                    <li>test</li>
                </ul>
            </div>
        </div>
    );
  }

export default function Projects(){
    const matches = useMediaQuery('(max-width:700px)');
    if(matches){
        var xs = 12;
    }else{
        xs = 6;
    }
    const classes = useStyles();
    return(
        <div>
            <Grid container spacing={3}>
                <Grid item xs={xs}>
                    <Paper className={classes.paper} elevation={10}><Content title="Conway's game of life" content="A simple version of Conways game of life written in javascript with react. This project taught me how to manipulate html elements with the use of react hooks." url="https://github.com/CostaAspro708"/></Paper>
                </Grid>
                <Grid item xs={xs}>
                    <Paper className={classes.paper} elevation={10}>xs={xs}</Paper>
                </Grid>
                <Grid item xs={xs}>
                    <Paper className={classes.paper} elevation={10}>xs={xs}</Paper>
                </Grid>
            </Grid>
        </div>
    );
}

